import {RegisterInsightsQuery} from '@github-ui/insights-charts'
import {sendEvent} from '@github-ui/hydro-analytics'
import {sendStats} from '@github-ui/stats'

window.addEventListener('insights-query-datadog-telemetry', (event: Event) => {
  const e = event as CustomEvent
  const stats: PlatformBrowserStat = {}
  let eventIncrementKey = e.detail.incrementKey
  if (eventIncrementKey) {
    eventIncrementKey = `INSIGHTS_QUERY_${eventIncrementKey.replace(/-/g, '_').toUpperCase()}`
    const incrementKey = eventIncrementKey as PlatformBrowserIncrementKey
    if (incrementKey) {
      stats['incrementKey'] = incrementKey
    }
  }

  // Sending counter metrics to datadog.
  if (Object.keys(stats).length > 0) {
    sendStats(stats)
  }
})

window.addEventListener('insights-query-hydro-telemetry', (event: Event) => {
  const e = event as CustomEvent
  let eventIncrementKey = e.detail.incrementKey
  if (eventIncrementKey) {
    eventIncrementKey = `INSIGHTS_QUERY_${eventIncrementKey.replace(/-/g, '_').toUpperCase()}`
  }

  // Sending metrics to hydro
  sendEvent(eventIncrementKey, e.detail.context || {})
})

RegisterInsightsQuery()
